import React, { useEffect, useRef, useState } from "react";
import { Button, Space } from "antd-mobile";
import "./recorder.css";
const FILE_SLIPT = 10;
const STEP = 3000 / FILE_SLIPT;
export default function Recorder({ stream }: { stream: MediaStream }) {
  // const videoRef = useRef<HTMLVideoElement>(null);

  const [controls, setControls] = useState<{
    isStart: boolean;
    isPause: boolean;
  }>({
    isStart: false,
    isPause: false,
  });
  const startTimesRef = useRef<number>(0);
  var basicOptions = { mimeType: "video/x-matroska" }; // 设置编码格式
  const basicRecordMedia = useRef<MediaRecorder>(
    new MediaRecorder(stream, basicOptions)
  );
  const recordList = useRef<
    Array<{
      chunks: Blob[];
      recorder: MediaRecorder;
    }>
  >([]);

  const download = () => {
    const chunks = recordList?.current?.[0]?.chunks;

    if (!chunks) {
      console.log("no data...");
      return;
    }
    var blob = new Blob(chunks, { type: "video/x-matroska" });
    var url = URL.createObjectURL(blob);
    var a: any = document.createElement("a");
    document.body.appendChild(a);
    a.style = "display: none";
    a.href = url;
    a.download = "video.mp4";
    a.click();
    window.URL.revokeObjectURL(url);
  };

  const start = () => {
    console.time();
    var startRecord = () => {
      let recorder;
      console.log("正在录制。。。");
      if (controls.isStart && recordList?.current.length >= 1) {
        return;
      }
      if (recordList?.current.length >= 5) {
        const tempRecord = recordList?.current.shift();
        recorder = tempRecord?.recorder;
        recorder?.requestData();
        tempRecord && (tempRecord.chunks = []);
      } else {
        recorder = new MediaRecorder(stream, {
          mimeType: "video/x-matroska",
        });
        recorder.start();
      }
      if (!recorder) return;
      const chunks: Blob[] = [];

      recorder.ondataavailable = (e: any) => {
        chunks.push(e.data);
      };

      recorder.onstop = (e: any) => {};
      recordList?.current.push({
        recorder,
        chunks,
      });
    };
    basicRecordMedia?.current?.start(FILE_SLIPT);
    basicRecordMedia.current.onstop = (e: any) => {};
    basicRecordMedia.current.ondataavailable = (e: any) => {
      startTimesRef.current++;
      if (startTimesRef.current < STEP) {
        startRecord();
        startTimesRef.current = startTimesRef.current + STEP;
      }
      if (startTimesRef.current % STEP === 0) {
        startRecord();
      }
    };
    //  setControls((pre) => ({ ...pre, isStart: true }));
  };
  const stop = () => {
    console.log("结束录制。。。");
    console.timeEnd();

    recordList?.current.forEach((item) => {
      item?.recorder && item?.recorder?.stop();
      //   item?.chunks && (item.chunks = []);
    });
    basicRecordMedia?.current?.stop();
    //  // 开始下载
    // handle.download();
    setControls((pre) => ({ ...pre, isStart: false }));
    setTimeout(() => {
      download();
      init();
    }, 1000);
  };
  const pause = () => {
    recordList?.current.forEach((item) => {
      item?.recorder && item?.recorder?.pause();
    });
    basicRecordMedia?.current?.pause();

    setControls((pre) => ({ ...pre, isPause: true }));
  };
  const resume = () => {
    recordList?.current.forEach((item) => {
      item?.recorder && item?.recorder?.resume();
    });
    basicRecordMedia?.current?.resume();
    setControls((pre) => ({ ...pre, isPause: false }));
  };
  const init = () => {
    recordList.current = [];
    startTimesRef.current = 0;
    start();
  };

  useEffect(() => {
    start();
  }, []);
  // function buttonHandle(e: React.MouseEvent<HTMLElement, MouseEvent>) {
  //   const { className } = e.target as HTMLElement;
  //   if (className in handle) {
  //     // @ts-ignore
  //     handle[className]();
  //   }
  // }
  return (
    <div>
      <div className="footer">
        {!controls?.isStart ? (
          <Button
            onClick={() => {
              setControls((pre) => ({ ...pre, isStart: true }));
            }}
            color="primary"
            fill="outline"
          >
            开始录制
          </Button>
        ) : null}
        {controls?.isStart ? (
          <Space block direction="horizontal" justify="around">
            <Button
              onClick={() => {
                stop();
              }}
              color="danger"
            >
              停止并保存
            </Button>
            {!controls?.isPause ? (
              <Button onClick={pause} color="warning">
                暂停
              </Button>
            ) : (
              <Button onClick={resume} color="primary" fill="outline">
                继续
              </Button>
            )}
          </Space>
        ) : null}
      </div>
    </div>
  );
}
